const baseHsl = `291, 0%, 18%`;

const colors = {
  black: `hsla(${baseHsl},1)`, // #2E2E2E
  text: `hsla(${baseHsl},0.95)`,
  light: `hsla(${baseHsl},0.4)`,
  calm: `hsla(${baseHsl},0.2)`,
  smoke: `hsla(${baseHsl},0.1)`,
  whiteSmoke: `hsla(${baseHsl},0.02)`,
  white: `#fff`,
};

export default colors;



